body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #3C4B62;
  color: white;
}

.App-header {
  text-align: center;
  height: 150px;
  padding: 20px;
}

.App-intro {
  font-size: large;
}

.tile {
  height: 150px;
  width: 150px;
  margin: 10px;
  background: #bbffbb;
  color: black;
  float: left;
  font-size: .85rem;
  text-align: left;
}

.newIdeaButton {
  background: #F5F4F4;
  ;
  color: black;
  border: none;
  font-size: 18px;
  cursor: pointer;
  /* margin-right: 10px;
  margin-left: 10px;
  padding: 10px; */
  margin: 1rem;
  padding: 1rem;
}

.input {
  border: 0;
  background: none;
  outline: none;
  margin-top: 10px;
  width: 140px;
  font-size: .85rem;
}

.input:focus {
  border: solid 1px lightgrey;
}

textarea {
  resize: none;
  height: 90px;
  font-size: 11px;
}

.sub {
  width: 100%;
  border-radius: 0%;
  background-color: rgb(193, 113, 48);
  color: white;
  font-size: 1rem;
}

.deleteButton {
  visibility: hidden;
  float: right;
  margin: 5px;
  font-size: 14px;
  cursor: pointer;
  color: red; 
}

.tile:hover .deleteButton {
  visibility: visible;
}

.card-tile {
  height: 150px;
  width: 150px;
  margin: 0px !important;
  background: #bbffbb !important;
  color: black;
  float: left;
  font-size: 1rem;
  text-align: left;
  border-radius: 0 !important;
}

.card-delete {
  float: right;
}
